<template>
    <div class="search">
        <header>
            <a href="javascript:history.back(-1)">
                <i class="el-icon-arrow-left"></i>
            </a>
            <span>搜索</span>
        </header>
        <section class="s1">
            <el-input placeholder="请输入商家或美食名称" clearable v-model="keyword" v-on:input="change" class="s1_input"></el-input>
            <span @click="resT1">提交</span>
        </section>
        <section class="s2" v-if="isShow2">
            <h4>商家</h4>
            <ul>
                <li v-for="s in search" @click="toShop(s)">
                    <img :src="'https://elm.cangdu.org/img/'+s.image_path" class="img">
                    <div class="s21">
                        <p><span>{{s.name}}</span><span class="s22">支付</span></p>
                        <p>月售{{s.recent_order_num}}单</p>
                        <p>{{s.float_minimum_order_amount}}元起送 / 距离{{s.distance}}</p>
                    </div>
                </li>
            </ul>
        </section>
        <section class="s3" v-if="isShow3">很抱歉！无搜索结果</section>
        <section class="s4" v-if="isShow4">
            <h4>搜索历史</h4>
            <ul>
                <li v-for="(s,index) in searchS" >
                    <span @click="resT2(s)">{{s}}</span>
                    <i class="el-icon-close" @click="clearThis(index)"></i>
                </li>
            </ul>
            <div @click="clearAll">清空搜索历史</div>
        </section>
        <footer>
            <router-link to="/msite"><div><i class="el-icon-eleme"></i><p>外卖</p></div></router-link>
            <router-link to=""><div class="f1"><i class="el-icon-search"></i> <p >搜索</p></div></router-link>
            <router-link to="/order"><div><i class="el-icon-tickets"></i><p>订单</p></div></router-link>
            <router-link to="/profile"><div><i class="el-icon-user"></i> <p>我的</p></div></router-link>
        </footer>
    </div>
</template>

<script>
    export default {
        name: "Search",
        data(){
            return{
                keyword:"",
                geohash:"",
                search:[],
                isShow2:false,
                isShow3:false,
                isShow4:false,
                searchS:[],
            }
        },
        created(){
            this.geohash = JSON.parse(localStorage.getItem("msite")).geohash;
            if(localStorage.getItem("search")){
                this.searchS = JSON.parse(localStorage.getItem("search"));
                if (this.searchS != ""){
                    this.isShow4 = true;
                }else {
                    this.isShow4 = false;
                }
            }

        },
        methods:{
            resT1(){
                if (this.keyword != ""){
                    this.restaurants();
                    let s;
                    if(localStorage.getItem("search")==null){
                        s = [];
                        s.push(this.keyword);
                        localStorage.setItem("search",JSON.stringify(s));
                    }else {
                        s = JSON.parse(localStorage.getItem("search"));
                        s.push(this.keyword);
                        localStorage.setItem("search",JSON.stringify(s));
                    }
                }

            },
            resT2(s){
                this.keyword = s;
                this.restaurants();
            },
            toShop(s){
                this.$router.push("/shop");
                localStorage.setItem("shop",JSON.stringify(s));
            },
            change(){
                if(this.keyword == ""){
                    this.isShow2 = false;
                    this.isShow3 = false;
                    this.isShow4 = true;
                }
            },
            restaurants(){
                this.axios.get("/v4/restaurants",{
                    params:{
                        geohash:this.geohash,
                        keyword:this.keyword,
                    }
                }).then(res=>{
                    if(res == "") {
                        this.isShow3 = true;
                    }else {
                        this.search = res.splice(0,20);
                        this.isShow2 = true;
                        console.log(this.search);
                    }
                }).catch(err=>{});this.axios.get("/v4/restaurants",{
                    params:{
                        geohash:this.geohash,
                        keyword:this.keyword,
                    }
                }).then(res=>{
                    if(res == "") {
                        this.isShow3 = true;
                        this.isShow2 = false;
                        this.isShow4 = false;
                    }else {
                        this.search = res.splice(0,20);
                        this.isShow2 = true;
                        this.isShow3 = false;
                        this.isShow4 = false;
                        console.log(this.search);
                    }
                }).catch(err=>{});
            },
            clearThis(i){
                this.searchS.splice(i,1);
                localStorage.setItem("search",JSON.stringify(this.searchS));
                if(this.searchS == ""){
                    this.isShow4 = false;
                }
            },
            clearAll(){
                this.searchS = [];
                localStorage.removeItem("search");
                this.isShow4 = false;
            }
        },
    }
</script>

<style scoped>
    .search{
        width: 100%;
        padding-top: 1.95rem;
        overflow: hidden;
    }
    a{
        text-decoration: none;
    }
    header>a{
        color: #fff;
        position: absolute;
        top: 50%;
        left: .4rem;
        transform: translateY(-50%);
    }
    header span{
        font-size: .8rem;
        color: #fff;
        font-weight: 700;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
    }
    .s1{
        background-color: #fff;
        padding: .5rem;
        display: flex;
    }
    .s1_input{
        flex: 4;
        font-size: .65rem;
        color: #333;
        border-radius: .125rem;
        background-color: #f2f2f2;
        font-weight: 700;
        outline: none;
    }
    .s1>span{
        flex: 1;
        border: .025rem solid #3190e8;
        margin-left: .2rem;
        font-size: .65rem;
        color: #fff;
        border-radius: .125rem;
        background-color: #3190e8;
        font-weight: 700;
        padding: 0 .25rem;
        text-align: center;
        line-height: 1.7rem;
    }
    .s2 h4,.s4 h4{
        font-size: .6rem;
        line-height: 2rem;
        text-indent: .5rem;
        font-weight: 700;
        color: #666;
    }
    .s2 ul{
        background-color: #fff;
    }
    .s2 li{
        display: flex;
        padding: .5rem;
        border-bottom: .025rem solid #e4e4e4;
    }
    .img{
        width: 1.7rem;
        height: 1.7rem;
        margin-right: .25rem;
    }
    .s21{
        font-size: .55rem;
        flex: 1;
        padding-bottom: .25rem;
        border-bottom: .025rem solid #e4e4e4;
    }
   .s21 p{
       line-height: .9rem;
   }
   .s22{
       display: inline-block;
       border:0.05rem solid rgb(255,136,64);
       border-bottom: 0.1rem solid rgb(255,136,64);
       color:rgb(255,136,64);
       font-size: 0.3rem;
       line-height: 0.57rem;
       margin-left: 0.2rem;
   }
    .s3{
        margin: 0 auto;
        font: .65rem/1.75rem Microsoft YaHei;
        color: #333;
        background-color: #fff;
        text-align: center;
        margin-top: .125rem;
    }
    .s4 li{
        background-color: #fff;
        border-bottom: .025rem solid #e4e4e4;
        font: .7rem/2rem Microsoft YaHei;
        padding: 0 .3rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .s4 li span{
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        display: inline-block;
        width: 80%;
    }
    .s4>div{
        background-color: #fff;
        color: #3190e8;
        font: .7rem/2rem Microsoft YaHei;
        font-weight: 700;
        text-align: center;
    }
    footer{
        background-color: #fff;
        position: fixed;
        z-index: 100;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 1.95rem;

        box-shadow: 0 -0.02667rem 0.05333rem rgba(0,0,0,.1);
    }
    footer div{
        width: 25%;
        height: 1.95rem;
        text-align: center;
        align-items: center;
        float: left;
    }

    footer i{
        width: .8rem;
        height: .8rem;
        margin: .3rem 0 .15rem;
        color: #666;
    }
    .f1 i{
        color:#3190e8;
    }
    footer p{
        font-size: .45rem;
        color: #666;
    }
</style>
